}

.screen{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  min-height: 150px;
  position: relative;
  margin-bottom: 20px;
  padding: 5px;
  background: #9ead86;
  border: 5px solid;
  border-top-color: currentcolor;
  border-right-color: currentcolor;
  border-bottom-color: currentcolor;
  border-left-color: currentcolor;
  border-color: #987f0f rgb(255,210,0) rgb(255,205,0) #987f0f;
}
#scaler{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  width: 0; /*calculated via js to keep board aspect ratio*/
  height: 100%;
  margin: 0 auto;
}
.table{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0;
  border-bottom: 5px solid black;
}
.row{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0;
  margin: 0;
  height: 100%;
}
.col{
  width: 100%;
  position: relative;
  margin: 0;
  border: 2px solid #879372;
  padding: 0.08em; /*inside border*/
}
.col::after{
  content: "";
  display: block;
  width:  100%;
  height: 100%;
  background: #8E9A79;
}
.filled{
  border-color: black;
}
.filled::after{
  background: black;
}
.empty{
  border-color: #879372;
}

.debug-container{
  position: absolute;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.debug-board{
  color: white;
  font-size: 12px;
  width:  100px !important; 
  height: 160px !important;
  padding-bottom: 20px;
}

.gameover{
  display: -webkit-flex;
  display: flex;
  width:  100%;
  height: 100%;
  position: absolute;
  z-index: 98;
  margin: -5px;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.gameover span{
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
  z-index: 100;
  -webkit-align-self: center;
          align-self: center;
  font-weight: 800;
  text-decoration: underline;
  text-shadow: 0 0 20px black;
  -webkit-text-stroke: 1.5px #65704e;
}

@media(max-width: 768px){
  .main-container{
    width:  100%;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  .gameboy{
    max-width: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0;
    border-radius: 0;
    max-height: 100%;
    max-width:  100%;
  }
  .debug-board{
    display: none;
  }
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.info{
    padding: 0;
    padding: 1px;
    padding-left: 5px;
    padding-right: 5px;
    margin: 4px -5px -5px -5px;
    background: #8e9d76;
    color: black;
    font-size: 15px;
    font-weight: 800;
    text-align: left;
    border-top: 3px solid #7F8E67;
}

.score{
    float: right;
    white-space: pre-wrap;
}

.blink {
    animation: blink-animation 1s steps(2, start) infinite;
    -webkit-animation: blink-animation 1s steps(2, start) infinite;
}
@keyframes blink-animation {
    to {
        visibility: hidden;
    }
}
@-webkit-keyframes blink-animation {
    to {
        visibility: hidden;
    }
}
:focus{ 
    outline: none; 
}
button::-moz-focus-inner {
    border: 0;
}
    

.controls{
    position: relative;
}
.controls .container{
    position: relative;
    text-align: left;
    overflow: auto;
    padding: 2px;
}
.controls .spacer{
    position: relative;
    display: inline-block;
    width:  40px;
    height: 40px;
    text-align: center;
    background: rgb(30,30,30);
}
.controls .spacer .arrows{
    display: inline-block;
    width:  100%;
    height: 100%;
    background: rgb(40,40,40);
    box-shadow: inset 0px 0px 5px rgba(0,0,0,1);
    border-radius: 30px;
    margin: 0;
}
.controls .buttons{
    font-size: 13px;
    font-weight: 700;
    float: left;
    padding: 2px;
}
.controls .button{
    display: inline-block;
    text-align: center;
}
.controls .button + .button{
    margin-left: 10px;
}
.controls .buttons button{
    width:  28px;
    height: 28px;
    border-radius: 50%;
    border: none;
    box-shadow: 0px 0px 5px black;
    background: rgb(155,0,0);
    margin-bottom: 5px;
}
.controls .buttons button:active{
    box-shadow: inset 0px 0px 5px 5px rgba(0,0,0,0.3);
}

.controls .buttons .drop{
    display: block;
    margin: 0;
    margin-top: 10px;
}
.controls .buttons .drop button{
    width:  60px;
    height: 60px;
}



.controls .arrows{
    margin: 5px;
    float: right;
}
.controls .arrows .left-right{
    display: -webkit-flex;
    display: flex;
}
.controls .arrows button{
    width:  35px;
    height: 40px;
    font-size: 20px;
    border: none;
    text-align: center;
    padding: 0 10px;
    color: #ffffff;
    background: rgb(30,30,30);
    box-shadow: 0px 0px 5px rgba(0,0,0,1);
}
.controls .arrows .arrow-up{
    width:  40px;
    height: 35px;
}
.controls .arrows .arrow-down{
    width:  40px;
    height: 35px;
}
.controls .arrows button:active{
    box-shadow: inset 1px 0px 5px rgba(0,0,0,1);
}


/*mini arrows*/
i{
    border: solid white;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 2px;
    position: absolute;
}
.spacer .up{
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    top: 7px;
    left: 43%;
}
.spacer .down{
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    bottom: 7px;
    left: 43%;
}
.spacer .left{
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    left: 7px;
    top: 43%;
}
.spacer .right{
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    right: 7px;
    top: 43%;
}
