/*custom number inputs*/
input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.number-input {
  display: -webkit-inline-flex;
  display: inline-flex;
  color: #4F5E37;
  height: 20px;
  width: 100%;
  box-sizing: border-box;
  background: #86956E;
  border: 2px solid #7F8E67;
  margin: 0.5px;
}

.number-input,
.number-input * {
  box-sizing: border-box;
}

.number-input button {
  outline:none;
  border: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
  margin: 0;
  position: relative;
  background: #86956E;
  color: #4F5E37;
}
.number-input input[type=number] {
  width: 40%;
  border: none;
  text-align: center;
  background: #8e9d76;
  color: #4F5E37;
}
